<template>
  <div class="login_container">
    <div class="login_box">
      <!--  -->
      <div class="login_avatar">
        <img src="../assets/logo.png" alt />
      </div>
      <!--  -->
      <el-form
        :model="formData"
        :rules="rules"
        label-width="0px"
        class="login_form"
        ref="formDataRef"
      >
        <!--  -->
        <el-form-item prop="email">
          <el-input v-model="formData.email" prefix-icon="fa fa-user"></el-input>
        </el-form-item>
        <!--  -->
        <el-form-item prop="pw">
          <el-input type="password" v-model="formData.pw" prefix-icon="fa fa-key"></el-input>
        </el-form-item>
        <!--  -->
        <el-form-item class="btns">
          <el-button type="primary" @click="login"></el-button>
          <el-button @click="resetLoginForm"></el-button>
        </el-form-item>
      </el-form>
    </div>
  </div>
</template>
<script>
export default {
  data() {
    // 
    var checkEmail = (rule, value, cb) => {
      // 
      const regEmail = /^[a-zA-Z0-9.!#$%&'*+/=?^_`{|}~-]+@[a-zA-Z0-9](?:[a-zA-Z0-9-]{0,61}[a-zA-Z0-9])?(?:\.[a-zA-Z0-9](?:[a-zA-Z0-9-]{0,61}[a-zA-Z0-9])?)*$/
      if (regEmail.test(value)) {
        //  cb
        return cb()
      }
      cb(new Error(''))
    }
    return {
      // 
      formData: {
        email: 'admin@linncy.cn',
        pw: '12345678'
      },
      rules: {
        email: [
          { required: true, message: '', trigger: 'blur' },
          { validator: checkEmail, trigger: 'blur' }
        ],
        pw: [
          { required: true, message: '', trigger: 'blur' },
          { min: 3, max: 36, message: ' 3  36', trigger: 'blur' }
        ]
      }
    }
  },
  methods: {
    resetLoginForm() {
      // resetFields
      this.$refs.formDataRef.resetFields()
    },
    login() {
      this.$refs.formDataRef.validate(async valid => {
        if (!valid) return
        //  res
        const { data: res } = await this.$http.post('login', this.formData)
        console.log(this)
        if (res.meta.status === 400) return this.$Message.error(res.meta.msg)
        this.$Message.success(res.meta.msg)
        window.sessionStorage.setItem('token', res.data.token)
        // console.log(res)
        this.$router.push('/home')
      })
    }
  }
}
</script>

<style lang="less" scoped>
.login_container {
  position: relative;
  height: 100%;
  background-color: aqua;
}
.login_box {
  position: absolute;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
  width: 50%;
  height: 320px;
  background-color: #fff;
  margin: 0 auto;
  // 
  .login_avatar {
    position: absolute;
    left: 50%;
    transform: translate(-50%, -50%);
    width: 130px;
    height: 130px;
    padding: 10px;
    border: 1px solid #eee;
    border-radius: 50%;
    box-shadow: 0 0 10px #ddd;
    background-color: #fff;
    img {
      width: 100%;
      height: 100%;
      border-radius: 50%;
      background-color: #eee;
    }
  }
  // 
  .login_form {
    position: absolute;
    bottom: 0;
    width: 100%;
    padding: 0 10px;
    box-sizing: border-box;
    //
    .btns {
      display: flex;
      justify-content: flex-end;
    }
  }
}
</style>
