<template>
  <div>
    <!--  -->
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item :to="{ path: '/home' }"></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
    </el-breadcrumb>
    <!--  -->
    <el-card>
      <div style="margin-top: 15px;">
        <el-row :gutter="20">
          <el-col :span="6">
            <el-input placeholder="" v-model="queryInfo.query" clearable @clear="searchKey">
              <el-button slot="append" icon="el-icon-search" @click="searchKey"></el-button>
            </el-input>
          </el-col>
          <el-col :span="6">
            <el-button type="primary" @click="addDialogVisible=true"></el-button>
          </el-col>
        </el-row>
      </div>
      <!--  -->
      <el-table :data="tableData" style="width: 100%" border stripe>
        <el-table-column type="index"></el-table-column>
        <el-table-column prop="username" label="" width="180"></el-table-column>
        <el-table-column prop="email" label="" width="180"></el-table-column>
        <el-table-column prop="mobile" label="" width="180"></el-table-column>
        <el-table-column prop="roleid.roleName" label="" width="120"></el-table-column>
        <el-table-column prop="status" label="" width="100">
          <!--propslot-scope='scope'tableDate -->
          <template slot-scope="scope">
            <!-- scope.row -->
            <el-switch
              v-model="scope.row.status"
              active-color="#13ce66"
              inactive-color="#ff4949"
              @change="userStatusChange(scope.row)"
            ></el-switch>
          </template>
        </el-table-column>
        <el-table-column label="">
          <template slot-scope="scope">
            <el-button
              type="primary"
              icon="el-icon-edit"
              size="mini"
              @click="editUser(scope.row._id)"
            ></el-button>
            <el-button
              type="danger"
              icon="el-icon-delete"
              size="mini"
              @click="delectUser(scope.row._id)"
            ></el-button>
            <!--  -->
            <el-tooltip
              class="item"
              effect="dark"
              content=""
              placement="top"
              :enterable="false"
            >
              <el-button
                type="warning"
                icon="el-icon-setting"
                size="mini"
                @click="setRole(scope.row)"
              ></el-button>
            </el-tooltip>
          </template>
        </el-table-column>
      </el-table>
      <!-- pagesize,page-sizes @size-change
      pagesize,page-sizes-->
      <!-- @current-change -->
      <el-pagination
        @size-change="handleSizeChange"
        @current-change="handleCurrentChange"
        :current-page="queryInfo.currentpage"
        :page-sizes="[5, 2, 10, 20]"
        :page-size="queryInfo.pagesize"
        layout="total, sizes, prev, pager, next, jumper"
        :total="queryInfo.total"
      ></el-pagination>
      <!--  -->
      <!-- visible.sync -->
      <!-- before-close -->
      <!-- close -->
      <el-dialog title="" :visible.sync="addDialogVisible" width="50%" @close="addDialogClose">
        <!--  -->
        <el-form
          :model="addForm"
          :rules="addRuleForm"
          ref="addRuleFormref"
          label-width="100px"
          class="demo-ruleForm"
        >
          <el-form-item label="" prop="username">
            <el-input v-model="addForm.username"></el-input>
          </el-form-item>
          <el-form-item label="" prop="password">
            <el-input v-model="addForm.password"></el-input>
          </el-form-item>
          <el-form-item label="" prop="email">
            <el-input v-model="addForm.email"></el-input>
          </el-form-item>
          <el-form-item label="" prop="mobile">
            <el-input v-model="addForm.mobile"></el-input>
          </el-form-item>
        </el-form>
        <!--  -->
        <span slot="footer" class="dialog-footer">
          <el-button @click="addDialogVisible = false"> </el-button>
          <el-button type="primary" @click="addUser"> </el-button>
        </span>
      </el-dialog>
      <!--  -->
      <el-dialog
        title=""
        :visible.sync="editDialogVisible"
        width="50%"
        @close="editDialogClose"
      >
        <!--  -->
        <el-form :model="editForm" :rules="editRuleForm" ref="editRuleFormref" label-width="100px">
          <el-form-item label="" prop="username">
            <el-input v-model="editForm.username"></el-input>
          </el-form-item>
          <el-form-item label="">
            <el-input v-model="editForm.email" disabled></el-input>
          </el-form-item>
          <el-form-item label="" prop="mobile">
            <el-input v-model="editForm.mobile"></el-input>
          </el-form-item>
        </el-form>
        <!--  -->
        <span slot="footer" class="dialog-footer">
          <el-button @click="editDialogVisible = false"> </el-button>
          <el-button type="primary" @click="modifyUser"> </el-button>
        </span>
      </el-dialog>
      <!--  -->
      <el-dialog title="" :visible.sync="setRoledialogVisible" width="50%">
        <div>
          <p>{{userInfo.username}}</p>
          <p>{{roleCurrent}}</p>
          <p>
            
            <el-select v-model="selectedRoleId" placeholder="">
              <el-option
                v-for="item in rolesList"
                :key="item._id"
                :label="item.roleName"
                :value="item._id"
              ></el-option>
            </el-select>
          </p>
        </div>
        <span slot="footer" class="dialog-footer">
          <el-button @click="setRoledialogVisible = false"> </el-button>
          <el-button type="primary" @click="saveRoleInfo"> </el-button>
        </span>
      </el-dialog>
    </el-card>
  </div>
</template>
<script>
export default {
  data() {
    // 
    var checkEmail = (rule, value, cb) => {
      // 
      const regEmail = /^[a-zA-Z0-9.!#$%&'*+/=?^_`{|}~-]+@[a-zA-Z0-9](?:[a-zA-Z0-9-]{0,61}[a-zA-Z0-9])?(?:\.[a-zA-Z0-9](?:[a-zA-Z0-9-]{0,61}[a-zA-Z0-9])?)*$/
      if (regEmail.test(value)) {
        //  cb
        return cb()
      }
      cb(new Error(''))
    }
    var checkMobile = (rule, value, cb) => {
      // 
      const regMobile = /^(?:(?:\+|00)86)?1(?:(?:3[\d])|(?:4[5-7|9])|(?:5[0-3|5-9])|(?:6[5-7])|(?:7[0-8])|(?:8[\d])|(?:9[1|8|9]))\d{8}$/
      if (regMobile.test(value)) {
        //  cb
        return cb()
      }
      cb(new Error(''))
    }
    return {
      // 
      tableData: [],
      // 
      queryInfo: {
        // 
        query: '',
        // 
        currentpage: 1,
        // 
        pagesize: 5,
        // 
        total: 0
      },
      // 
      addDialogVisible: false,
      // 
      addForm: {
        username: '166666',
        password: '12345678',
        email: 'aaaa123@qq.com',
        mobile: '13254780987'
      },
      // 
      addRuleForm: {
        username: [
          { required: true, message: '', trigger: 'blur' },
          { min: 2, max: 30, message: ' 2  30 ', trigger: 'blur' }
        ],
        password: [
          { required: true, message: '', trigger: 'blur' },
          { min: 6, max: 30, message: ' 6  30 ', trigger: 'blur' }
        ],
        email: [
          { required: true, message: '', trigger: 'blur' },
          { validator: checkEmail, trigger: 'blur' }
        ],
        mobile: [
          { required: true, message: '', trigger: 'blur' },
          { validator: checkMobile, trigger: 'blur' }
        ]
      },
      // 
      editForm: {},
      // 
      editRuleForm: {
        username: [
          { required: true, message: '', trigger: 'blur' },
          { min: 2, max: 30, message: ' 2  30 ', trigger: 'blur' }
        ],
        mobile: [
          { required: true, message: '', trigger: 'blur' },
          { validator: checkMobile, trigger: 'blur' }
        ]
      },
      // 
      editDialogVisible: false,
      // 
      setRoledialogVisible: false,
      // 
      userInfo: '',
      roleCurrent: '',
      // 
      selectedRoleId: '',
      // 
      rolesList: []
    }
  },
  created() {
    this.initUser()
  },
  methods: {
    // 
    async initUser() {
      const users = await this.$http.get('/users', {
        params: this.queryInfo
      })
      this.tableData = users.data.data.records
      this.queryInfo.total = users.data.data.total
      // console.log(this.tableData)
      // this.queryInfo.pagesize = users.data.data.pagesize
      // console.log(users.data.data)
    },
    // pagesize
    handleSizeChange(NewSize) {
      if (this.queryInfo.query && this.queryInfo.query.trim().length !== 0) {
        this.queryInfo.pagesize = NewSize
        this.searchKey()
      } else {
        this.queryInfo.pagesize = NewSize
        this.initUser()
      }
    },
    // 
    handleCurrentChange(Currentpage) {
      if (this.queryInfo.query && this.queryInfo.query.trim().length !== 0) {
        this.queryInfo.currentpage = Currentpage
        this.searchKey()
      } else {
        this.queryInfo.currentpage = Currentpage
        this.initUser()
      }
    },
    // 
    async userStatusChange(userInfo) {
      // console.log(userInfo)
      const { data: res } = await this.$http.put('/users/' + userInfo._id, {
        status: userInfo.status
      })
      if (res.meta.status !== 200) {
        userInfo.status = !userInfo.status
        this.$Message.error('')
      }
      this.$Message.success('')
      // console.log(res)
    },
    // 
    async searchKey() {
      if (this.queryInfo.query && this.queryInfo.query.trim().length !== 0) {
        const { data: res } = await this.$http.get(
          '/users/search/' + this.queryInfo.query,
          {
            params: this.queryInfo
          }
        )
        // console.log(res)
        this.tableData = res.data.records
        this.queryInfo.total = res.data.total
        // this.queryInfo.pagesize = res.data.pagesize
      } else {
        this.initUser()
      }
    },
    // 
    addDialogClose() {
      // 
      this.$refs.addRuleFormref.resetFields()
    },
    // 
    addUser() {
      // console.log(this.$refs.addRuleFormref);
      // return
      this.$refs.addRuleFormref.validate(async valide => {
        // 
        if (!valide) {
          return
        }
        const { data: res } = await this.$http.post('/users', this.addForm)
        this.addDialogVisible = false
        this.$Message.success(res.meta.msg)
        //  
        this.initUser()
      })
    },
    // 
    editDialogClose() {
      // 
      this.$refs.editRuleFormref.resetFields()
    },
    // 
    async editUser(id) {
      // console.log(id);
      const { data: res } = await this.$http.get('/users/' + id)
      if (res.meta.status !== 200) return
      console.log(res.data)
      this.editForm = res.data
      // rolenumber
      this.editForm.role = this.editForm.role.toString()
      // console.log(typeof this.editForm.role)
      this.editDialogVisible = true
    },
    // 
    modifyUser() {
      this.$refs.editRuleFormref.validate(async valide => {
        console.log(valide)
        if (!valide) return this.$Message.error('')
        const { data: res } = await this.$http.put(
          '/users/' + this.editForm._id,
          {
            username: this.editForm.username,
            mobile: this.editForm.mobile,
            role: this.editForm.role
          }
        )
        // console.log(res)
        if (res.meta.status !== 200) return this.$Message.error(res.meta.msg)
        // 
        this.editDialogVisible = false
        this.$Message.success('')
        // 
        this.initUser()
      })
    },
    // 
    async delectUser(id) {
      const isVauleUser = await this.$confirm(
        ', ?',
        '',
        {
          confirmButtonText: '',
          cancelButtonText: '',
          type: 'warning'
        }
      ).catch(err => err)
      console.log(isVauleUser)
      if (isVauleUser === 'cancel') return this.$Message.info('')
      const { data: res } = await this.$http.delete('/users/' + id)
      if (res.meta.status !== 200) return this.$Message.error(res.meta.msg)
      this.$Message.success(res.meta.msg)
      //  
      this.initUser()
    },
    // 
    async setRole(userInfo) {
      this.userInfo = userInfo
      this.roleCurrent = userInfo.roleid.roleName
      const { data: res } = await this.$http.get('/roles')
      if (res.meta.status !== 200) return this.$Message.error(res.meta.msg)
      this.rolesList = res.data
      this.setRoledialogVisible = true
    },
    async saveRoleInfo() {
      console.log(this.selectedRoleId)
      this.setRoledialogVisible = false
      const { data: res } = await this.$http.put(
        '/users/' + this.userInfo._id + '/role',
        {
          rid: this.selectedRoleId
        }
      )
      if (res.meta.status !== 200) return this.$Message.error(res.meta.msg)
      this.$Message.success(res.meta.msg)
      this.selectedRoleId = ''
      this.initUser()
    }
  }
}
</script>
<style lang="less" scoped>
</style>
