<template>
  <div>
    <!--  -->
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item :to="{ path: '/' }"></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
    </el-breadcrumb>
    <!--  -->
    <el-card class="box-card">
      <!--  -->
      <el-alert title="" type="warning" show-icon></el-alert>
      <!--  -->
      <el-row class="cat_opt">
        <el-col>
          <span></span>
          <!--  -->
          <!-- option  -->
          <!--  props  -->
          <!-- v-model , -->
          <!-- clearable  -->
          <!-- change-on-select  -->
          <!--  @change -->
          <el-cascader
            expandTrigger="hover"
            v-model="selectedKeys"
            :options="CategoryList"
            :props="cascaderProps"
            @change="CategoryChanged"
            clearable
          ></el-cascader>
        </el-col>
      </el-row>
      <!--@tab-click: tab  -->
      <!--v-model  name -->
      <!--name  value  -->
      <el-tabs v-model="activeName" @tab-click="handleTabsClick">
        <!--  -->
        <el-tab-pane label="" name="many">
          <el-button
            type="primary"
            size="mini"
            :disabled="isBtnDisabled"
            @click="addDialogVisible = true"
          ></el-button>
          <el-table :data="manyTableData" style="width: 100%">
            <el-table-column type="expand">
              <template slot-scope="scope">
                <!-- tag -->
                <el-tag
                  v-for="(tag,index) in scope.row.attr_vals"
                  :key="index"
                  closable
                  @close="handleClose(scope.row,index)"
                  type="success"
                >{{tag}}</el-tag>
                <!--  -->
                <el-input
                  class="input-new-tag"
                  v-if="scope.row.inputVisible"
                  v-model="scope.row.inputValue"
                  ref="saveTagInput"
                  size="small"
                  @keyup.enter.native="handleInputConfirm(scope.row)"
                  @blur="handleInputConfirm(scope.row)"
                ></el-input>
                <el-button
                  v-else
                  class="button-new-tag"
                  size="small"
                  @click="showInput(scope.row)"
                >+ New Tag</el-button>
              </template>
            </el-table-column>
            <el-table-column type="index"></el-table-column>
            <el-table-column prop="attr_name" label=""></el-table-column>
            <el-table-column label="">
              <template slot-scope="scope">
                <el-button
                  type="primary"
                  icon="el-icon-edit"
                  size="mini"
                  @click="showEditParmas(scope.row)"
                ></el-button>
                <el-button
                  type="danger"
                  icon="el-icon-delete"
                  size="mini"
                  @click="delectParams(scope.row)"
                ></el-button>
              </template>
            </el-table-column>
          </el-table>
        </el-tab-pane>
        <!--  -->
        <el-tab-pane label="" name="only">
          <el-button
            type="primary"
            size="mini"
            :disabled="isBtnDisabled"
            @click="addDialogVisible = true"
          ></el-button>
          <el-table :data="onlyTableData" style="width: 100%">
              <el-table-column type="expand">
              <template slot-scope="scope">
                <!-- tag -->
                <el-tag
                  v-for="(tag,index) in scope.row.attr_vals"
                  :key="index"
                  closable
                  @close="handleClose(scope.row,index)"
                  type="success"
                >{{tag}}</el-tag>
                <!--  -->
                <el-input
                  class="input-new-tag"
                  v-if="scope.row.inputVisible"
                  v-model="scope.row.inputValue"
                  ref="saveTagInput"
                  size="small"
                  @keyup.enter.native="handleInputConfirm(scope.row)"
                  @blur="handleInputConfirm(scope.row)"
                ></el-input>
                <el-button
                  v-else
                  class="button-new-tag"
                  size="small"
                  @click="showInput(scope.row)"
                >+ New Tag</el-button>
              </template>
            </el-table-column>
            <el-table-column type="index"></el-table-column>
            <el-table-column prop="attr_name" label=""></el-table-column>
            <el-table-column label="">
              <template slot-scope="scope">
                <el-button
                  type="primary"
                  icon="el-icon-edit"
                  size="mini"
                  @click="showEditRole(scope.row)"
                ></el-button>
                <el-button
                  type="danger"
                  icon="el-icon-delete"
                  size="mini"
                  @click="delectRole(scope.row._id)"
                ></el-button>
              </template>
            </el-table-column>
          </el-table>
        </el-tab-pane>
      </el-tabs>
      <!--  -->
      <el-dialog
        :title="''+titleText"
        :visible.sync="addDialogVisible"
        width="50%"
        @close="addDialogClosed"
      >
        <div>
          <el-form
            :model="addParamsForm"
            :rules="addParamsFormRules"
            ref="addParamsFormRef"
            label-width="100px"
            class="demo-ruleForm"
          >
            <el-form-item :label="titleText" prop="attr_name">
              <el-input v-model="addParamsForm.attr_name" @keyup.enter.native="addParams"></el-input>
            </el-form-item>
          </el-form>
        </div>
        <span slot="footer" class="dialog-footer">
          <el-button @click="addDialogVisible = false"> </el-button>
          <el-button type="primary" @click="addParams"> </el-button>
        </span>
      </el-dialog>
      <!--  -->
      <el-dialog
        :title="''+titleText"
        :visible.sync="editDialogVisible"
        width="50%"
        @close="editDialogClosed"
      >
        <div>
          <el-form
            :model="editParamsForm"
            :rules="editParamsFormRules"
            ref="editParamsFormRef"
            label-width="100px"
            class="demo-ruleForm"
          >
            <el-form-item :label="titleText" prop="attr_name">
              <el-input v-model="editParamsForm.attr_name" @keyup.enter.native="editParams"></el-input>
            </el-form-item>
          </el-form>
        </div>
        <span slot="footer" class="dialog-footer">
          <el-button @click="editDialogVisible = false"> </el-button>
          <el-button type="primary" @click="editParams"> </el-button>
        </span>
      </el-dialog>
    </el-card>
  </div>
</template>
<script>
export default {
  data() {
    return {
      // 
      CategoryList: [],
      // 
      cascaderProps: {
        // CategoryList cat_idcat_name,children
        // 
        value: 'cat_id',
        // 
        label: 'cat_name',
        // 
        children: 'children'
      },
      // 
      selectedKeys: [],
      // Tab 
      // Tabs 
      //  activeName
      activeName: 'many',
      // 
      manyTableData: [],
      // 
      onlyTableData: [],
      // 
      addDialogVisible: false,
      // 
      addParamsForm: {
        attr_name: ''
      },
      // 
      addParamsFormRules: {
        attr_name: [
          { required: true, message: '', trigger: 'blur' },
          { min: 2, max: 30, message: ' 2  30 ', trigger: 'blur' }
        ]
      },
      // 
      editDialogVisible: false,
      // 
      editParamsForm: {
        attr_name: '',
        attr_id: '',
        attr_vals: ''
      },
      // 
      editParamsFormRules: {
        attr_name: [
          { required: true, message: '', trigger: 'blur' },
          { min: 2, max: 30, message: ' 2  30 ', trigger: 'blur' }
        ]
      }
      // // tag 
      // inputVisible: false,
      // // tag
      // inputValue: ''
    }
  },
  created() {
    this.getCategoryList()
  },
  // 
  computed: {
    // true, false
    isBtnDisabled() {
      if (this.selectedKeys.length !== 3) {
        return true
      }
      return false
    },
    // id
    cateId() {
      if (this.selectedKeys.length === 3) {
        return this.selectedKeys[2]
      }
      return null
    },
    titleText() {
      if (this.activeName === 'many') {
        return ''
      }
      return ''
    }
  },
  methods: {
    // 
    async getCategoryList() {
      const { data: res } = await this.$http.get('/categories')
      console.log(res)
      if (res.meta.status !== 200) return this.$Message.error(res.meta.msg)
      // children
      this.getCategorySimply(res.data)
      this.CategoryList = res.data
    },
    // childrenchildren
    getCategorySimply(arr) {
      arr.forEach(item => {
        if (item.children.length === 0) {
          // 
          delete item.children
        } else {
          this.getCategorySimply(item.children)
        }
      })
    },
    // 
    CategoryChanged() {
      this.getCatParams()
    },
    // tabs 
    handleTabsClick() {
      console.log(this.activeName)
      this.getCatParams()
    },
    // 
    async getCatParams() {
      // 
      if (this.selectedKeys.length !== 3) {
        this.selectedKeys = []
        // 
        this.manyTableData = []
        // 
        this.onlyTableData = []
        return false
      }
      // 
      const { data: res } = await this.$http.get(
        '/categories/' + this.cateId + '/attributes',
        {
          params: {
            sel: this.activeName
          }
        }
      )
      if (res.meta.status !== 200) {
        return this.$Message.error('')
      }
      res.data.forEach(item => {
        item.attr_vals = item.attr_vals ? item.attr_vals.split(' ') : []
        // tag
        item.inputVisible = false
        // tag 
        item.inputValue = ''
      })
      // console.log(res.data)
      if (this.activeName === 'many') {
        this.manyTableData = res.data
      } else {
        this.onlyTableData = res.data
      }
    },
    // 
    async addParams() {
      this.$refs.addParamsFormRef.validate(async valide => {
        // 
        if (!valide) {
          return
        }
        const { data: res } = await this.$http.post(
          '/categories/' + this.cateId + '/attributes',
          {
            attr_name: this.addParamsForm.attr_name,
            attr_sel: this.activeName
          }
        )
        if (res.meta.status !== 200) {
          return this.$Message.error(res.meta.msg)
        }
        this.$Message.success(res.meta.msg)
        this.getCatParams()
      })
      this.addDialogVisible = false
    },
    // 
    addDialogClosed() {
      this.$refs.addParamsFormRef.resetFields()
    },
    // 
    showEditParmas(params) {
      this.editParamsForm.attr_name = params.attr_name
      this.editParamsForm.attr_id = params._id
      this.editParamsForm.attr_vals = params.attr_vals
      this.editDialogVisible = true
    },
    // 
    async editParams() {
      this.$refs.editParamsFormRef.validate(async valide => {
        // categories/:id/attributes/:attrId
        // 
        if (!valide) {
          return
        }
        const { data: res } = await this.$http.put(
          `categories/${this.cateId}/attributes/${this.editParamsForm.attr_id}`,
          {
            attr_name: this.editParamsForm.attr_name,
            attr_sel: this.activeName,
            attr_vals: this.editParamsForm.attr_vals.join(' ')
          }
        )
        if (res.meta.status !== 200) {
          return this.$Message.error(res.meta.msg)
        }
        this.$Message.success(res.meta.msg)
        this.getCatParams()
        // console.log(res)
      })
      this.editDialogVisible = false
    },
    // 
    editDialogClosed() {
      this.$refs.editParamsFormRef.resetFields()
    },
    // 
    async delectParams(params) {
      const isVauleUser = await this.$confirm(
        ', ?',
        '',
        {
          confirmButtonText: '',
          cancelButtonText: '',
          type: 'warning'
        }
      ).catch(err => err)
      // console.log(isVauleUser)
      if (isVauleUser === 'cancel') return this.$Message.info('')
      const { data: res } = await this.$http.delete(
        `categories/${this.cateId}/attributes/${params._id}`
      )
      if (res.meta.status !== 200) return this.$Message.error(res.meta.msg)
      this.$Message.success(res.meta.msg)
      this.getCatParams()
    },
    // tag
    async handleInputConfirm(row) {
      // 
      if (row.inputValue.trim().length === 0) {
        row.inputValue = ''
        row.inputVisible = false
        return ''
      }
      row.attr_vals.push(row.inputValue.trim())
      row.inputValue = ''
      row.inputVisible = false
      this.saveParams(row)
    },
    // tag 
    showInput(params) {
      params.inputVisible = true
      // 
      // $nextTick 
      // 
      this.$nextTick(_ => {
        this.$refs.saveTagInput.$refs.input.focus()
      })
    },
    // attr_vals
    async saveParams(row) {
      const { data: res } = await this.$http.put(
        `categories/${this.cateId}/attributes/${row._id}`,
        {
          attr_name: row.attr_name,
          attr_sel: row.attr_sel,
          attr_vals: row.attr_vals.join(' ')
        }
      )
      if (res.meta.status !== 200) {
        return this.$Message.error(res.meta.msg)
      }
      this.$Message.success(res.meta.msg)
    },
    handleClose(row, index) {
      row.attr_vals.splice(index, 1)
      this.saveParams(row)
    }
  }
}
</script>
<style lang="less" scoped>
.cat_opt {
  margin: 15px 0;
}
.el-tag {
  margin: 10px 0px;
  margin-right: 10px;
}
.input-new-tag {
  width: 120px;
}
</style>
