  mounted() {
    // 1.Dom,echarts
    var myChart = echarts.init(document.getElementById('main'))
    // 
    var option = {
      title: {
        text: ''
      },
      tooltip: {
        trigger: 'axis',
        axisPointer: {
          type: 'cross',
          label: {
            backgroundColor: '#E9EEF3'
          }
        }
      },
      grid: {
        left: '3%',
        right: '4%',
        bottom: '3%',
        containLabel: true
      },
      xAxis: [
        {
          boundaryGap: false
        }
      ],
      yAxis: [
        {
          type: 'value'
        }
      ]
    }
    // this.options option
    const result = _.merge(this.options, option)
    // 3.
    myChart.setOption(result)
  }
}
</script>
