<template>
  <div>
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item :to="{ path: '/' }"></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
    </el-breadcrumb>
    <el-card class="box-card">
      <el-row>
        <el-col :span="8">
          <el-input placeholder="" v-model="queryInfo.query" clearable @clear="searchKey" @keyup.enter="searchKey">
            <el-button slot="append" icon="el-icon-search" @click="searchKey"></el-button>
          </el-input>
        </el-col>
      </el-row>
      <el-table :data="AllOrdersData" border style="width: 100%" height="450">
        <el-table-column type="index" label="#"></el-table-column>
        <el-table-column prop="order_number" label=""></el-table-column>
        <el-table-column prop="order_price" label="" width="80" align="center"></el-table-column>
        <el-table-column prop="pay_status" label="" width="80" align="center">
            <template slot-scope="scope">
            <el-tag type="success" v-if="scope.row.pay_status === '1'"></el-tag>
            <el-tag type="danger" v-else></el-tag>
          </template>
        </el-table-column>
        <el-table-column prop="is_send" label="" width="80" align="center"></el-table-column>
        <el-table-column prop="create_time" label="" width="160" align="center">
          <template slot-scope="scope">{{scope.row.create_time | dateformat}}</template>
        </el-table-column>
        <el-table-column label="" width="120" align="center">
          <!-- slot-scope="scope" -->
          <template slot-scope="scope">
            <el-button
              type="primary"
              icon="el-icon-edit"
              size="mini"
              @click="editById(scope.row._id)"
            ></el-button>
            <el-button type="danger" icon="el-icon-setting" size="mini" @click="showprogress"></el-button>
          </template>
        </el-table-column>
      </el-table>
      <el-pagination
        @size-change="handleSizeChange"
        @current-change="handleCurrentChange"
        :current-page="queryInfo.currentpage"
        :page-sizes="[1, 2, 5, 10]"
        :page-size="queryInfo.pagesize"
        layout="total, sizes, prev, pager, next, jumper"
        :total="queryInfo.total"
      ></el-pagination>
      <!--  -->
      <el-dialog
        title=""
        :visible.sync="editDialogVisible"
        width="50%"
        @close="editDialogClose"
      >
        <!--  -->
        <el-form
          :model="editForm"
          :rules="editFormRule"
          ref="editRuleFormref"
          label-width="100px"
          label-position="top"
        >
          <el-form-item label="/" prop="address1">
            <el-cascader
              expandTrigger="hover"
              v-model="editForm.address1"
              :options="cityData"
              clearable
            ></el-cascader>
          </el-form-item>
          <el-form-item label="" prop="address2">
            <el-input v-model="editForm.address2"></el-input>
          </el-form-item>
        </el-form>
        <!--  -->
        <span slot="footer" class="dialog-footer">
          <el-button @click="editDialogVisible = false"> </el-button>
          <el-button type="primary" @click="modifyAdress"> </el-button>
        </span>
      </el-dialog>
      <!--  -->
      <el-dialog
        title=""
        :visible.sync="progressDialogVisible"
        width="50%"
        @close="progressDialogClose"
      >
        <!--  -->
        <el-timeline>
          <el-timeline-item
            v-for="(activity, index) in kuaidi"
            :key="index"
            :timestamp="activity.time"
          >{{activity.context}}</el-timeline-item>
        </el-timeline>
        <!--  -->
        <span slot="footer" class="dialog-footer">
          <el-button @click="progressDialogVisible = false"> </el-button>
          <el-button type="primary" @click="progressDialogVisible = false"> </el-button>
        </span>
      </el-dialog>
    </el-card>
  </div>
</template>
<script>
import cityData from './citydata'
import kuaidi from './kuaidi'
export default {
  data() {
    return {
      // 
      AllOrdersData: [],
      // 
      queryInfo: {
        // 
        query: '',
        // 
        currentpage: 1,
        // 
        pagesize: 5,
        // 
        total: 0
      },
      // 
      editForm: {
        id: '',
        address1: [],
        address2: ''
      },
      // 
      editFormRule: {
        address1: [
          { required: true, message: '/', trigger: 'blur' }
        ],
        address2: [
          { required: true, message: '', trigger: 'blur' }
        ]
      },
      editDialogVisible: false,
      // 
      cityData,
      kuaidi,
      progressDialogVisible: false
    }
  },
  // Vue
  created() {
    this.getOrdersList()
  },
  methods: {
    // 
    async getOrdersList() {
      const { data: res } = await this.$http.get('/orders', {
        params: {
          currentpage: this.queryInfo.currentpage,
          pagesize: this.queryInfo.pagesize
        }
      })
      // console.log(res)
      if (res.meta.status !== 200) return this.$Message.error(res.meta.msg)
      this.AllOrdersData = res.data.records
      this.queryInfo.total = res.data.total
    },
    // 
    handleSizeChange(newSize) {
      if (this.queryInfo.query && this.queryInfo.query.trim().length !== 0) {
        this.queryInfo.currentpage = newSize
        this.searchKey()
      } else {
        this.queryInfo.pagesize = newSize
        this.getOrdersList()
      }
    },
    // 
    handleCurrentChange(newPage) {
      if (this.queryInfo.query && this.queryInfo.query.trim().length !== 0) {
        this.queryInfo.currentpage = newPage
        this.searchKey()
      } else {
        this.queryInfo.currentpage = newPage
        this.getOrdersList()
      }
    },
    // 
    async searchKey() {
      if (this.queryInfo.query && this.queryInfo.query.trim().length !== 0) {
        const { data: res } = await this.$http.get(
          '/orders/search/' + this.queryInfo.query,
          {
            params: this.queryInfo
          }
        )
        // console.log(res)
        this.AllOrdersData = res.data.records
        this.queryInfo.total = res.data.total
        // this.queryInfo.pagesize = res.data.pagesize
      } else {
        this.getOrdersList()
      }
    },
    // 
    async editById(id) {
      const { data: res } = await this.$http.get(`/orders/${id}`)
      if (res.meta.status !== 200) return this.$Message.error(res.meta.msg)
      this.editForm.id = res.data._id
      if (res.data.consignee_addr.trim().length > 0) {
        const arr = res.data.consignee_addr.split('  ')
        console.log(arr)
        if (arr.length >= 2) {
          // 
          this.editForm.address1 = arr[0].split(' ')
          var arr2 = arr.splice(1)
          this.editForm.address2 = arr2.join('')
        } else {
          this.editForm.address1 = []
          this.editForm.address2 = ''
        }
      }
      this.editDialogVisible = true
    },
    // 
    editDialogClose() {
      // 
      this.$refs.editRuleFormref.resetFields()
      this.editForm.address1 = []
      this.editForm.address2 = ''
    },
    modifyAdress() {
      this.$refs.editRuleFormref.validate(async valide => {
        // console.log(valide)
        if (!valide) return this.$Message.error('')
        console.log(this.editForm.address1.join('') + this.editForm.address2)
        const { data: res } = await this.$http.put(
          '/orders/' + this.editForm.id,
          {
            consignee_addr: [
              this.editForm.address1.join(' '),
              this.editForm.address2
            ]
          }
        )
        console.log(res)
        if (res.meta.status !== 200) return this.$Message.error(res.meta.msg)
        // 
        this.editDialogVisible = false
        this.$Message.success('')
        // 
        this.getOrdersList()
      })
    },
    // 
    showprogress() {
      this.progressDialogVisible = true
    },
    // 
    progressDialogClose() {}
  }
}
</script>
<style lang="less" scoped>
@import '../../plugins/timeline/timeline.css';
@import '../../plugins/timeline-item/timeline-item(1).css';
.btn {
  margin-left: 10px;
}
.el-cascader {
  width: 100%;
}
</style>
