<template>
  <div>
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item :to="{ path: '/' }"></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
    </el-breadcrumb>
    <el-card class="box-card">
      <el-row>
        <el-col :span="8">
          <el-input placeholder="" v-model="queryInfo.query" clearable @clear="searchKey">
            <el-button slot="append" icon="el-icon-search" @click="searchKey"></el-button>
          </el-input>
        </el-col>
        <el-col :span="4">
          <el-button type="primary" class="btn" @click="goAddPage"></el-button>
        </el-col>
      </el-row>
      <el-table :data="AllGoodsData" border style="width: 100%" height="450">
        <el-table-column type="index" label="#"></el-table-column>
        <el-table-column prop="goods_name" label=""></el-table-column>
        <el-table-column prop="goods_price" label="" width="80" align="center"></el-table-column>
        <el-table-column prop="goods_number" label="" width="80" align="center"></el-table-column>
        <el-table-column prop="upd_time" label="" width="160" align="center">
          <template slot-scope="scope">{{scope.row.upd_time | dateformat}}</template>
        </el-table-column>
        <el-table-column label="" width="120" align="center">
          <!-- slot-scope="scope" -->
          <template slot-scope="scope">
            <el-button
              type="primary"
              icon="el-icon-edit"
              size="mini"
              @click="editById(scope.row._id)"
            ></el-button>
            <el-button
              type="danger"
              icon="el-icon-delete"
              size="mini"
              @click="removeById(scope.row._id)"
            ></el-button>
          </template>
        </el-table-column>
      </el-table>
      <el-pagination
        @size-change="handleSizeChange"
        @current-change="handleCurrentChange"
        :current-page="queryInfo.currentpage"
        :page-sizes="[1, 2, 5, 10]"
        :page-size="queryInfo.pagesize"
        layout="total, sizes, prev, pager, next, jumper"
        :total="queryInfo.total"
      ></el-pagination>
      <!--  -->
      <el-dialog
        title=""
        :visible.sync="editDialogVisible"
        width="50%"
        @close="editDialogClose"
      >
        <!--  -->
        <el-form
          :model="editForm"
          :rules="editFormRule"
          ref="editRuleFormref"
          label-width="100px"
          label-position="top"
        >
          <el-form-item label="" prop="goods_name">
            <el-input v-model="editForm.goods_name"></el-input>
          </el-form-item>
          <el-form-item label="" prop="goods_price">
            <el-input v-model="editForm.goods_price"></el-input>
          </el-form-item>
          <el-form-item label="" prop="goods_number">
            <el-input v-model="editForm.goods_number"></el-input>
          </el-form-item>
          <el-form-item label="" prop="goods_weight">
            <el-input v-model="editForm.goods_weight"></el-input>
          </el-form-item>
        </el-form>
        <!--  -->
        <span slot="footer" class="dialog-footer">
          <el-button @click="editDialogVisible = false"> </el-button>
          <el-button type="primary" @click="modifyGood"> </el-button>
        </span>
      </el-dialog>
    </el-card>
  </div>
</template>
<script>
export default {
  data() {
    return {
      // 
      AllGoodsData: [],
      // 
      queryInfo: {
        // 
        query: '',
        // 
        currentpage: 1,
        // 
        pagesize: 5,
        // 
        total: 0
      },
      // 
      editForm: {
        id: '',
        goods_name: '',
        goods_price: 0,
        goods_weight: 0,
        goods_number: 0
      },
      // 
      editFormRule: {
        goods_name: [
          { required: true, message: '', trigger: 'blur' }
        ],
        goods_price: [
          { required: true, message: '', trigger: 'blur' }
        ],
        goods_weight: [
          { required: true, message: '', trigger: 'blur' }
        ],
        goods_number: [
          { required: true, message: '', trigger: 'blur' }
        ]
      },
      editDialogVisible: false
    }
  },
  created() {
    this.getGoodsList()
  },
  methods: {
    // 
    async getGoodsList() {
      const { data: res } = await this.$http.get('/goods', {
        params: {
          currentpage: this.queryInfo.currentpage,
          pagesize: this.queryInfo.pagesize
        }
      })
      // console.log(res)
      if (res.meta.status !== 200) return this.$Message.error(res.meta.msg)
      this.AllGoodsData = res.data.records
      this.queryInfo.total = res.data.total
    },
    // 
    handleSizeChange(newSize) {
      if (this.queryInfo.query && this.queryInfo.query.trim().length !== 0) {
        this.queryInfo.currentpage = newSize
        this.searchKey()
      } else {
        this.queryInfo.pagesize = newSize
        this.getGoodsList()
      }
    },
    // 
    handleCurrentChange(newPage) {
      if (this.queryInfo.query && this.queryInfo.query.trim().length !== 0) {
        this.queryInfo.currentpage = newPage
        this.searchKey()
      } else {
        this.queryInfo.currentpage = newPage
        this.getGoodsList()
      }
    },
    // id
    async removeById(id) {
      // id_id
      const isVauleUser = await this.$confirm(
        ', ?',
        '',
        {
          confirmButtonText: '',
          cancelButtonText: '',
          type: 'warning'
        }
      ).catch(err => err)
      // console.log(isVauleUser)
      if (isVauleUser === 'cancel') return this.$Message.info('')
      const { data: res } = await this.$http.delete(`/goods/${id}`)
      console.log(res)
      if (res.meta.status !== 200) return this.$Message.error(res.meta.msg)
      this.$Message.success(res.meta.msg)
      this.getGoodsList()
    },
    goAddPage() {
      this.$router.push('/goods/add')
    },
    // 
    async searchKey() {
      if (this.queryInfo.query && this.queryInfo.query.trim().length !== 0) {
        const { data: res } = await this.$http.get(
          '/goods/search/' + this.queryInfo.query,
          {
            params: this.queryInfo
          }
        )
        // console.log(res)
        this.AllGoodsData = res.data.records
        this.queryInfo.total = res.data.total
        // this.queryInfo.pagesize = res.data.pagesize
      } else {
        this.getGoodsList()
      }
    },
    // 
    async editById(id) {
      const { data: res } = await this.$http.get(`/goods/${id}`)
      // console.log(res)
      if (res.meta.status !== 200) return this.$Message.error(res.meta.msg)
      this.editForm.goods_name = res.data.goods_name
      this.editForm.goods_price = res.data.goods_price
      this.editForm.goods_number = res.data.goods_number
      this.editForm.goods_weight = res.data.goods_weight
      this.editForm.id = res.data._id
      this.editDialogVisible = true
    },
    // 
    editDialogClose() {
      // 
      this.$refs.editRuleFormref.resetFields()
    },
    // 
    modifyGood() {
      this.$refs.editRuleFormref.validate(async valide => {
        // console.log(valide)
        if (!valide) return this.$Message.error('')
        const { data: res } = await this.$http.put(
          '/goods/' + this.editForm.id,
          {
            goods_name: this.editForm.goods_name,
            goods_price: this.editForm.goods_price,
            goods_number: this.editForm.goods_number,
            goods_weight: this.editForm.goods_weight
          }
        )
        // console.log(res)
        if (res.meta.status !== 200) return this.$Message.error(res.meta.msg)
        // 
        this.editDialogVisible = false
        this.$Message.success('')
        // 
        this.getGoodsList()
      })
    }
  }
}
</script>
<style lang="less" scoped>
.btn {
  margin-left: 10px;
}
</style>
