import Vue from 'vue'
import VueRouter from 'vue-router'
import Login from '../components/Login.vue'
import Home from '../components/Home.vue'
import Helloworld from '../components/Helloworld.vue'
import User from '../components/user/User.vue'
import Rights from '../components/Right/Rights.vue'
import Roles from '../components/Roles/Roles.vue'
import Categories from '../components/Categories/Categories.vue'
import Parmas from '../components/Categories/Parmas.vue'
import GoodsList from '../components/Categories/Goods_list.vue'
import AddGoods from '../components/Categories/AddGoods.vue'
import Order from '../components/order/order.vue'
import Report from '../components/Report/Report.vue'
// 
import '../assets/css/global.css'
// 
import '../assets/font/font-awesome/css/font-awesome.css'
// jsCss
import NProgress from 'nprogress'
import 'nprogress/nprogress.css'
import axios from 'axios'
// axios.defaults.baseURL = 'http://192.168.0.103:3000/api/private/v1/'
axios.defaults.baseURL = 'http://localhost:3000/api/private/v1/'
// token
axios.interceptors.request.use(config => {
  // console.log(config)
  NProgress.start()
  config.headers.Authorization = window.sessionStorage.getItem('token')
  //  return config
  return config
})
axios.interceptors.response.use(config => {
  // console.log(config)
  NProgress.done()
  //  return config
  return config
})
// vueaxios 
Vue.prototype.$http = axios
Vue.use(VueRouter)
const router = new VueRouter({
  routes: [
    { path: '/', redirect: '/login' },
    { path: '/login', component: Login },
    {
      path: '/home',
      component: Home,
      children: [
        { path: '/', component: Helloworld },
        { path: '/user', component: User },
        { path: '/rights', component: Rights },
        { path: '/roles', component: Roles },
        { path: '/categories', component: Categories },
        { path: '/params', component: Parmas },
        { path: '/goods', component: GoodsList },
        { path: '/goods/add', component: AddGoods },
        { path: '/order', component: Order },
        { path: '/report', component: Report }
      ]
    }
  ]
})
// 
router.beforeEach((to, from, next) => {
  // to and from are Route Object,next() must be called to resolve the hook}
  // 
  if (to.path === '/login') return next()
  // sessionStorage token 
  const tokenStr = window.sessionStorage.getItem('token')
  // token 
  if (!tokenStr) return next('/login')
  next()
})
export default router
