<template>
  <el-container class="home-container">
    <!--  -->
    <el-header>
      <div class="logo">
        <img src="../assets/heima.png" alt />
        <span></span>
      </div>
      <el-button type="primary" @click="logout"></el-button>
    </el-header>
    <!--  -->
    <el-container>
      <el-aside width="200px">
        <!--  -->
        <!-- unique-opened -->
        <el-menu
          background-color="#333744"
          text-color="#fff"
          active-text-color="#409EFF"
          unique-opened
          :collapse="isCollapse"
          :collapse-transition="false"
          router
          :default-active="activePath"
        >
          <!--  -->
          <el-submenu :key="item._id" v-for="item in menulist" :index="item._id">
            <!--  -->
            <template slot="title">
              <!--  -->
              <i class="fa" :class="item.className"></i>
              <!--  -->
              <span>{{item.authName}}</span>
            </template>
            <!--  -->
            <el-menu-item
              v-for="subItem in item.children"
              :key="subItem.authName"
              :index="subItem.path"
              @click="saveActivePath(subItem.path)"
            >
              <!--  -->
              <i class="fa" :class="subItem.className"></i>
              <!--  -->
              <span>{{subItem.authName}}</span>
            </el-menu-item>
          </el-submenu>
        </el-menu>
      </el-aside>
      <el-main>
        <!--  -->
        <router-view></router-view>
      </el-main>
    </el-container>
  </el-container>
</template>
<script>
export default {
  data() {
    return {
      // 
      menulist: [],
      // 
      isCollapse: false,
      // 
      activePath: ''
    }
  },
  // vue
  created() {
    this.getMenuList()
    this.activePath = window.sessionStorage.getItem('ActivePath')
  },
  methods: {
    logout() {
      // sessionStorage
      window.sessionStorage.clear()
      // login
      this.$router.push('/login')
    },
    // 
    async getMenuList() {
      const { data: res } = await this.$http.get('menus')
      if (res.meta.status !== 200) return this.$Message.error(res.meta.msg)
      this.menulist = res.data
      console.log(res)
    },
    // 
    saveActivePath(path) {
      window.sessionStorage.setItem('Active', path)
      this.activePath = path
    }
  }
}
</script>
<style lang="less" scoped>
.home-container {
  height: 100%;
  .el-header {
    background-color: rgba(0, 0, 0, 0.6);
    .logo {
      height: 60px;
      display: inline-block;
      img {
        height: 100%;
        vertical-align: middle;
      }
      span {
        margin-left: 20px;
        font-weight: 700;
        color: #fff;
      }
    }
    .el-button {
      float: right;
      margin-top: 10px;
    }
  }
  .el-aside {
    background-color: rgba(0, 0, 0, 0.5);
    height: 100%;
    .el-menu {
      i {
        vertical-align: middle;
        margin-right: 5px;
        width: 24px;
        text-align: center;
        font-size: 18px;
      }
    }
  }
  .el-main {
    height: 100%;
    background-color: #eee;
  }
}
</style>
