<template>
  <div>
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item :to="{ path: '/' }"></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
    </el-breadcrumb>
    <el-card class="box-card">
      <el-table :data="tableData" border style="width: 100%" stripe>
        <el-table-column type="index" width="50" label="#" align='center'></el-table-column>
        <el-table-column prop="authName" label="" align='center'></el-table-column>
        <el-table-column prop="path" label="" align='center'></el-table-column>
        <el-table-column label="" align='center'>
          <template slot-scope="scope">
            <el-tag :type="Tagtype[scope.row.level]">{{rightArr[scope.row.level]}}</el-tag>
          </template>
        </el-table-column>
      </el-table>
    </el-card>
  </div>
</template>
<script>
export default {
  data() {
    return {
      tableData: [],
      rightArr: ['', '', ''],
      Tagtype: ['success', 'info', 'danger']
    }
  },
  // vue
  created() {
    this.initPage()
  },
  methods: {
    async initPage() {
      const { data: res } = await this.$http.get('/rights')
      if (res.meta.status !== 200) return this.$Message.error(res.meta.msg)
      this.tableData = res.data
    }
  }
}
</script>
<style lang="less" scoped>
</style>
