<template>
  <div>
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item :to="{ path: '/' }"></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
    </el-breadcrumb>
    <el-card class="box-card" style="height:100%">
      <!--  -->
      <el-alert title="" type="info" show-icon></el-alert>
      <!--  -->
      <el-steps :space="200" :active="activePage - 0" finish-status="success" align-center>
        <el-step title=""></el-step>
        <el-step title=""></el-step>
        <el-step title=""></el-step>
        <el-step title=""></el-step>
        <el-step title=""></el-step>
        <el-step title=""></el-step>
      </el-steps>
      <el-form
        ref="addFormRef"
        :model="addForm"
        :rules="addFormRules"
        label-width="100px"
        label-position="top"
      >
        <el-tabs
          tab-position="left"
          v-model="activePage"
          :before-leave="beforeLeaveTab"
          @tab-click="clickTab"
        >
          <el-tab-pane label="" name="0">
            <el-form-item label="" prop="goods_name">
              <el-input v-model="addForm.goods_name"></el-input>
            </el-form-item>
            <el-form-item label="" prop="goods_price">
              <el-input v-model="addForm.goods_price" type="number"></el-input>
            </el-form-item>
            <el-form-item label="" prop="goods_weight">
              <el-input v-model="addForm.goods_weight" type="number"></el-input>
            </el-form-item>
            <el-form-item label="" prop="goods_number">
              <el-input v-model="addForm.goods_number" type="number"></el-input>
            </el-form-item>
            <el-col>
              <span></span>
              <!--  -->
              <!-- option  -->
              <!--  props  -->
              <!-- v-model , -->
              <!-- clearable  -->
              <!-- change-on-select  -->
              <!--  @change -->
              <el-cascader
                expandTrigger="hover"
                v-model="addForm.goods_cat"
                :options="CategoryList"
                :props="cascaderProps"
                @change="CategoryChanged"
                clearable
              ></el-cascader>
            </el-col>
          </el-tab-pane>
          <el-tab-pane label="" name="1">
            <el-form-item
              :label="item.attr_name"
              v-for="(item,index) in manyTableDate"
              :key="index"
            >
              <el-checkbox-group v-model="item.attr_vals">
                <el-checkbox
                  border
                  :label="item2"
                  v-for="(item2,index2) in item.attr_vals"
                  :key="index2"
                ></el-checkbox>
              </el-checkbox-group>
            </el-form-item>
          </el-tab-pane>
          <el-tab-pane label="" name="2">
            <el-form-item
              :label="item.attr_name"
              v-for="(item,index) in onlyTableDate"
              :key="index"
            >
              <el-input v-model="item.attr_vals"></el-input>
            </el-form-item>
          </el-tab-pane>
          <el-tab-pane label="" name="3">
            <el-upload
              class="upload-demo"
              :action="uploadUrl"
              :on-preview="handlePreview"
              :on-remove="handleRemove"
              list-type="picture"
              :headers="headerObj"
              :on-success="handleSuccess"
            >
              <el-button size="small" type="primary"></el-button>
              <!-- <div slot="tip" class="el-upload__tip">jpg/png500kb</div> -->
            </el-upload>
          </el-tab-pane>
          <el-tab-pane label="" name="4">
            <!--  -->
            <quill-editor ref="myQuillEditor" v-model="addForm.goods_introduce" />
            <el-button type="primary" class="btnAdd" @click="addGoods"></el-button>
          </el-tab-pane>
        </el-tabs>
      </el-form>
      <!--  -->
      <el-dialog title="" :visible.sync="previewDialogVisible" width="50%">
        <img :src="previewUrl" alt class="preview" />
        <span slot="footer" class="dialog-footer">
          <el-button @click="previewDialogVisible = false"> </el-button>
          <el-button type="primary" @click="previewDialogVisible = false"> </el-button>
        </span>
      </el-dialog>
    </el-card>
  </div>
</template>
<script>
import _ from 'lodash'
export default {
  data() {
    return {
      // 
      activePage: '0',
      // 
      addForm: {
        goods_name: '',
        goods_price: 0,
        goods_weight: 0,
        goods_number: 0,
        // 
        goods_cat: [],
        // 
        pics: [],
        // 
        goods_introduce: '',
        // id
        attrs: []
      },
      // 
      addFormRules: {
        goods_name: [
          { required: true, message: '', trigger: 'blur' }
        ],
        goods_price: [
          { required: true, message: '', trigger: 'blur' }
        ],
        goods_weight: [
          { required: true, message: '', trigger: 'blur' }
        ],
        goods_number: [
          { required: true, message: '', trigger: 'blur' }
        ]
      },
      // 
      CategoryList: [],
      // 
      cascaderProps: {
        // CategoryList cat_idcat_name,children
        // 
        value: 'cat_id',
        // 
        label: 'cat_name',
        // 
        children: 'children'
      },
      // 
      manyTableDate: [],
      // 
      onlyTableDate: [],
      // 
      uploadUrl: 'http://localhost:3000/api/private/v1/uploads',
      // headers
      headerObj: {
        Authorization: window.sessionStorage.getItem('token')
      },
      // 
      previewUrl: '',
      // 
      previewDialogVisible: false
    }
  },
  created() {
    this.getCategoryList()
  },
  // 
  computed: {
    cateId() {
      if (this.addForm.goods_cat.length === 3) {
        return this.addForm.goods_cat[2]
      }
      return null
    }
  },
  methods: {
    // 
    async getCategoryList() {
      const { data: res } = await this.$http.get('/categories')
      // console.log(res)
      if (res.meta.status !== 200) return this.$Message.error(res.meta.msg)
      // children
      this.getCategorySimply(res.data)
      this.CategoryList = res.data
    },
    // childrenchildren
    getCategorySimply(arr) {
      arr.forEach(item => {
        if (item.children.length === 0) {
          // 
          delete item.children
        } else {
          this.getCategorySimply(item.children)
        }
      })
    },
    // 
    CategoryChanged() {
      if (this.addForm.goods_cat.length !== 3) {
        this.addForm.goods_cat = []
      }
      // console.log(this.addForm.goods_cat)
    },
    beforeLeaveTab(activeName, oldActiveName) {
      if (oldActiveName === '0' && this.addForm.goods_cat.length !== 3) {
        return false
      }
    },
    async clickTab() {
      // console.log(this.activePage)
      if (this.activePage === '1') {
        // 
        const { data: res } = await this.$http.get(
          '/categories/' + this.cateId + '/attributes',
          {
            params: {
              sel: 'many'
            }
          }
        )
        if (res.meta.status !== 200) {
          return this.$Message.error('')
        }
        // console.log(res.data)
        res.data.forEach(item => {
          item.attr_vals = item.attr_vals ? item.attr_vals.split(' ') : []
        })
        this.manyTableDate = res.data
      } else if (this.activePage === '2') {
        // 
        const { data: res } = await this.$http.get(
          '/categories/' + this.cateId + '/attributes',
          {
            params: {
              sel: 'only'
            }
          }
        )
        if (res.meta.status !== 200) {
          return this.$Message.error('')
        }
        // console.log(res.data)
        this.onlyTableDate = res.data
      }
    },
    // 
    handlePreview(file) {
      console.log(file)
      this.previewUrl = file.response.data.url
      console.log(this.previewUrl)
      this.previewDialogVisible = true
    },
    // 
    handleRemove() {},
    // 
    handleSuccess(response) {
      // console.log(response)
      // 1. 
      const picInfo = {
        pic: response.data.tmp_path
      }
      // 2.
      this.addForm.pics.push(picInfo)
      // console.log(this.addForm)
    },
    // 
    addGoods() {
      this.$refs.addFormRef.validate(async valide => {
        // 
        if (!valide) {
          return this.$Message.error('')
        }
        this.manyTableDate.forEach(item => {
          const paramsInfo = {
            attr_id: item.attr_id,
            _id: item._id,
            attr_value: item.attr_vals
          }
          this.addForm.attrs.push(paramsInfo)
        })
        this.onlyTableDate.forEach(item => {
          //  "attr_id":15,
          //   "attr_value":"ddd"
          const paramsInfo = {
            attr_id: item.attr_id,
            attr_id_ref: item._id,
            attr_value: item.attr_vals
          }
          this.addForm.attrs.push(paramsInfo)
        })
        const form = _.cloneDeep(this.addForm)
        form.goods_cat = form.goods_cat.join(',')
        form.attrs = this.addForm.attrs
        // console.log(form)
        const { data: res } = await this.$http.post('/goods', form)
        this.addForm.attrs = []
        if (res.meta.status !== 200) return this.$Message.error(res.meta.msg)
        this.$refs.addFormRef.resetFields()
        this.$Message.success(res.meta.msg)
        this.$router.push('/goods')
      })
    }
  }
}
</script>
<style lang="less" scoped>
.el-steps {
  margin: 15px 0;
}
.el-checkbox {
  margin: 0 10px 0 0 !important;
}
.preview {
  width: 100%;
}
</style>
