<template>
  <div>
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item :to="{ path: '/' }"></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
    </el-breadcrumb>
    <el-card class="box-card">
      <el-button type="primary" @click="showAddCategory"></el-button>
      <!--  -->
      <tree-table
        class="treeTable"
        :data="treeData"
        :columns="columns"
        :selection-type="false"
        :expand-type="false"
        show-index
        index-text="#"
        border
        :show-row-hover="false"
      >
        <template slot="isok" scope="scope">
          <i
            class="el-icon-success"
            style="color: lightgreen;"
            v-if="scope.row.cat_deleted === false"
          ></i>
          <i class="el-icon-error" style="color: red;" v-else></i>
        </template>
        <template slot="order" scope="scope">
          <el-tag type="success" v-if="scope.row.cat_level === 0"></el-tag>
          <el-tag type="info" v-else-if="scope.row.cat_level === 1"></el-tag>
          <el-tag type="warning" v-else></el-tag>
        </template>
        <template slot="opt" scope="scope">
          <el-button
            type="primary"
            icon="el-icon-edit"
            size="small"
            @click="editCategory(scope.row)"
          ></el-button>
          <el-button
            type="danger"
            icon="el-icon-delete"
            size="small"
            @click="delCategory(scope.row)"
          ></el-button>
        </template>
      </tree-table>
      <!--  -->
      <el-pagination
        @size-change="handleSizeChange"
        @current-change="handleCurrentChange"
        :current-page="queryInfo.currentPage"
        :page-sizes="[1, 2, 5, 10]"
        :page-size="queryInfo.pagesize"
        layout="total, sizes, prev, pager, next, jumper"
        :total="queryInfo.total"
      ></el-pagination>
      <!--  -->
      <el-dialog
        title=""
        :visible.sync="addCategorydialogVisible"
        width="50%"
        @close="addCategoryDialogClosed"
      >
        <div>
          <el-form
            ref="addCategoryRef"
            :model="addCategoryForm"
            label-width="100px"
            :rules="addCategoryFormRule"
          >
            <el-form-item label="" prop="cat_name">
              <el-input v-model="addCategoryForm.cat_name"></el-input>
            </el-form-item>
            <el-form-item label="">
              <!-- option  -->
              <!--  props  -->
              <!-- v-model , -->
              <!-- clearable  -->
              <!-- change-on-select  -->
              <!--  @change -->
              <el-cascader
                expandTrigger="hover"
                v-model="selectedKeys"
                :options="parentCatList"
                :props="cascaderProps"
                @change="parentCateChanged"
                clearable
                change-on-select
              ></el-cascader>
            </el-form-item>
          </el-form>
        </div>
        <span slot="footer" class="dialog-footer">
          <el-button @click="addCategorydialogVisible = false"> </el-button>
          <el-button type="primary" @click="addCategory"> </el-button>
        </span>
      </el-dialog>
      <!--  -->
      <el-dialog
        title=""
        :visible.sync="editCategorydialogVisible"
        width="50%"
        @close="editCategoryDialogClosed"
      >
        <div>
          <el-form
            ref="editCategoryRef"
            :model="editCategoryForm"
            label-width="100px"
            :rules="editCategoryFormRule"
          >
            <el-form-item label="" prop="cat_name">
              <el-input v-model="editCategoryForm.cat_name"></el-input>
            </el-form-item>
          </el-form>
        </div>
        <span slot="footer" class="dialog-footer">
          <el-button @click="editCategorydialogVisible = false"> </el-button>
          <el-button type="primary" @click="saveEditCategory()"> </el-button>
        </span>
      </el-dialog>
    </el-card>
  </div>
</template>
<script>
export default {
  data() {
    return {
      // 
      treeData: [],
      // table
      columns: [
        {
          label: '',
          prop: 'cat_name'
        },
        {
          label: '',
          // 
          type: 'template',
          // 
          template: 'isok',
          //  'center', 'right'
          align: 'center',
          //  'center', 'right'
          headerAlign: 'center'
        },
        {
          label: '',
          // 
          type: 'template',
          // 
          template: 'order',
          //  'center', 'right'
          align: 'center',
          //  'center', 'right'
          headerAlign: 'center'
        },
        {
          label: '',
          // 
          type: 'template',
          // 
          template: 'opt',
          //  'center', 'right'
          align: 'center',
          //  'center', 'right'
          headerAlign: 'center'
        }
      ],
      // 
      queryInfo: {
        // 
        query: '',
        // 
        currentpage: 1,
        // 
        pagesize: 5,
        // 
        total: 0
      },
      // 
      addCategoryForm: {
        // 
        cat_name: '',
        // id 0
        cat_pid: 0,
        //  0,1
        cat_level: 0
      },
      // 
      addCategoryFormRule: {
        cat_name: [
          { required: true, message: '', trigger: 'blur' },
          { min: 2, max: 30, message: ' 2  30 ', trigger: 'blur' }
        ]
      },
      // 
      addCategorydialogVisible: false,
      // 
      parentCatList: [],
      // 
      cascaderProps: {
        // parentCatList cat_idcat_name,children
        // 
        value: 'cat_id',
        // 
        label: 'cat_name',
        // 
        children: 'children'
      },
      // 
      selectedKeys: [],
      // 
      editCategorydialogVisible: false,
      // 
      editCategoryForm: {
        // 
        cat_name: ''
      },
      // 
      editCategoryFormRule: {
        cat_name: [
          { required: true, message: '', trigger: 'blur' },
          { min: 2, max: 30, message: ' 2  30 ', trigger: 'blur' }
        ]
      },
      // id
      id: ''
    }
  },
  created() {
    this.initPage()
  },
  methods: {
    // 
    async initPage() {
      const { data: res } = await this.$http.get('/categories', {
        params: this.queryInfo
      })
      console.log(res.data)
      if (res.meta.status !== 200) return this.$Message.error(res.meta.msg)
      this.treeData = res.data.records
      this.queryInfo.total = res.data.total
    },
    // 
    handleCurrentChange(currentpage) {
      this.queryInfo.currentpage = currentpage
      this.initPage()
      console.log(this.queryInfo.currentpage)
    },
    // pagesize
    handleSizeChange(pagesize) {
      this.queryInfo.pagesize = pagesize
      this.initPage()
    },
    // 
    showAddCategory() {
      // 
      this.getParentCategory()
      this.addCategorydialogVisible = true
    },
    // 
    async getParentCategory() {
      const { data: res } = await this.$http.get('/categories', {
        params: { type: 2 }
      })
      console.log(res)
      if (res.meta.status !== 200) return this.$Message.error(res.meta.msg)
      // children
      this.getCategorySimply(res.data)
      this.parentCatList = res.data
      // console.log(res.data)
    },
    // 
    parentCateChanged() {
      // console.log(this.selectedKeys)
      // 
      if (this.selectedKeys.length > 0) {
        // 
        // id
        this.addCategoryForm.cat_pid = this.selectedKeys[
          this.selectedKeys.length - 1
        ]
        // 
        this.addCategoryForm.cat_level = this.selectedKeys.length
        // console.log(this.addCategoryForm)
      } else {
        // Id
        this.addCateForm.cat_pid = 0
        // 
        this.addCateForm.cat_level = 0
      }
    },
    // childrenchildren
    getCategorySimply(arr) {
      arr.forEach(item => {
        if (item.children.length === 0) {
          // 
          delete item.children
        } else {
          this.getCategorySimply(item.children)
        }
      })
    },
    // 
    addCategoryDialogClosed() {
      // 
      this.$refs.addCategoryRef.resetFields()
      //  Cascader 
      this.selectedKeys = []
      //   cat_id  cat_level
      this.addCategoryForm.cat_pid = 0
      this.addCategoryForm.cat_level = 0
      // console.log(this.addCategoryForm)
    },
    // 
    async addCategory() {
      // console.log(this.addCategoryForm)
      const { data: res } = await this.$http.post(
        '/categories',
        this.addCategoryForm
      )
      if (res.meta.status !== 200) return this.$Message.error(res.meta.msg)
      this.addCategorydialogVisible = false
      this.$Message.success(res.meta.msg)
      this.initPage()
    },
    // 
    async editCategory(category) {
      this.id = category._id
      this.editCategoryForm.cat_name = category.cat_name
      this.editCategorydialogVisible = true
    },
    // 
    async saveEditCategory() {
      const { data: res } = await this.$http.put(
        '/categories/' + this.id,
        this.editCategoryForm
      )
      if (res.meta.status !== 200) return this.$Message.error(res.meta.msg)
      this.editCategorydialogVisible = false
      this.$Message.success(res.meta.msg)
      this.initPage()
    },
    // 
    editCategoryDialogClosed() {
      this.editCategorydialogVisible = false
    },
    async delCategory(category) {
      const confirmResult = await this.$confirm(
        ', ?',
        '',
        {
          confirmButtonText: '',
          cancelButtonText: '',
          type: 'warning'
        }
      ).catch(err => err)
      if (confirmResult === 'cancel') {
        return this.$Message.info('')
      }
      console.log(category._id)
      const { data: res } = await this.$http.delete(
        '/categories/' + category._id
      )
      if (res.meta.status !== 200) return this.$Message.error(res.meta.msg)
      this.$Message.success(res.meta.msg)
      console.log(res)
      this.initPage()
    }
  }
}
</script>
<style lang="less" scoped>
.treeTable {
  margin-top: 15px;
  i {
    font-size: 20px;
  }
}
.el-cascader {
  width: 100%;
}
</style>
