<template>
  <div>
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item :to="{ path: '/' }"></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
    </el-breadcrumb>
    <el-card class="box-card">
      <el-button type="primary" @click="addRole"></el-button>
      <el-table :data="tableData" border style="width: 100%">
        <el-table-column type="expand">
          <template slot-scope="scope">
            <el-row
              v-for="(item1,index) in scope.row.children"
              :key="item1._id"
              :class="['dbbottom',index==0 ? 'dbtop':'']"
              class="vcenter"
            >
              <el-col :span="6">
                <el-tag
                  :closable="true"
                  @close="removeRightById(scope.row,item1._id)"
                >{{item1.authName}}</el-tag>
                <i class="el-icon-caret-right"></i>
              </el-col>
              <el-col :span="18">
                <el-row
                  v-for="item2 in item1.children"
                  :key="item2._id"
                  :class="['dbbottom']"
                  class="vcenter"
                >
                  <el-col :span="6">
                    <el-tag
                      type="success"
                      :closable="true"
                      @close="removeRightById(scope.row,item2._id)"
                    >{{item2.authName}}</el-tag>
                    <i class="el-icon-caret-right"></i>
                  </el-col>
                  <el-col :span="18">
                    <el-tag
                      v-for="item3 in item2.children"
                      :key="item3._id"
                      type="warning"
                      :closable="true"
                      @close="removeRightById(scope.row,item3._id)"
                    >{{item3.authName}}</el-tag>
                  </el-col>
                </el-row>
              </el-col>
            </el-row>
          </template>
        </el-table-column>
        <el-table-column type="index" width="50" label="#"></el-table-column>
        <el-table-column prop="roleName" label="" width="180"></el-table-column>
        <el-table-column prop="roleDesc" label="" width="180"></el-table-column>
        <el-table-column label="">
          <template slot-scope="scope">
            <el-button
              type="primary"
              icon="el-icon-edit"
              size="mini"
              @click="showEditRole(scope.row)"
            ></el-button>
            <el-button
              type="danger"
              icon="el-icon-delete"
              size="mini"
              @click="delectRole(scope.row._id)"
            ></el-button>
            <el-button
              type="warning"
              icon="el-icon-setting"
              size="mini"
              @click="showSetRightDialog(scope.row)"
            ></el-button>
          </template>
        </el-table-column>
      </el-table>
      <!--  -->
      <el-dialog
        title=""
        :visible.sync="showSetRightdialogVisible"
        width="50%"
        @close="setRightDialogClosed"
      >
        <!-- el-tree data -->
        <!-- show-checkbox  -->
        <!-- node-key  -->
        <!-- default-expand-all  -->
        <!-- props  labelchildren -->
        <el-tree
          :data="rightsList"
          :props="defaultProps"
          show-checkbox
          node-key="oid"
          default-expand-all
          :default-checked-keys="defaultKeys"
          ref="treeRef"
        ></el-tree>
        <span slot="footer" class="dialog-footer">
          <el-button @click="showSetRightdialogVisible = false"> </el-button>
          <el-button type="primary" @click="allotRight"> </el-button>
        </span>
      </el-dialog>
      <!--  -->
      <el-dialog
        title=""
        :visible.sync="addUserdialogVisible"
        width="50%"
        @close="addDialogClose"
      >
        <div>
          <el-form ref="addRoleRef" :model="addRoleForm" label-width="100px" :rules="addRoleRule">
            <el-form-item label="" prop="roleName">
              <el-input v-model="addRoleForm.roleName"></el-input>
            </el-form-item>
            <el-form-item label="" prop="roleDesc">
              <el-input v-model="addRoleForm.roleDesc"></el-input>
            </el-form-item>
          </el-form>
        </div>
        <span slot="footer" class="dialog-footer">
          <el-button @click="addUserdialogVisible = false"> </el-button>
          <el-button type="primary" @click="saveRole"> </el-button>
        </span>
      </el-dialog>
      <!--  -->
      <el-dialog title="" :visible.sync="editRoleDialogVisible" width="50%">
        <div>
          <el-form
            ref="editRoleRef"
            :model="editRoleForm"
            label-width="100px"
            :rules="editRoleRuleForm"
          >
            <el-form-item label="" prop="roleName">
              <el-input v-model="editRoleForm.roleName"></el-input>
            </el-form-item>
            <el-form-item label="" prop="roleDesc">
              <el-input v-model="editRoleForm.roleDesc"></el-input>
            </el-form-item>
          </el-form>
        </div>
        <span slot="footer" class="dialog-footer">
          <el-button @click="editRoleDialogVisible = false"> </el-button>
          <el-button type="primary" @click="saveEditRole"> </el-button>
        </span>
      </el-dialog>
    </el-card>
  </div>
</template>

<script>
export default {
  data() {
    return {
      // 
      tableData: [],
      // 
      showSetRightdialogVisible: false,
      // 
      rightsList: [],
      // 
      defaultProps: {
        label: 'authName',
        children: 'children'
      },
      //  key 
      defaultKeys: [],
      // id
      roleId: '',
      // 
      addUserdialogVisible: false,
      // 
      addRoleForm: {
        roleName: '',
        roleDesc: ''
      },
      // 
      addRoleRule: {
        roleName: [
          { required: true, message: '', trigger: 'blur' },
          { min: 1, max: 10, message: ' 1  10 ', trigger: 'blur' }
        ],
        roleDesc: [
          { required: true, message: '', trigger: 'blur' }
        ]
      },
      // 
      editRoleForm: {
        roleName: '',
        roleDesc: ''
      },
      // 
      editRoleRuleForm: {
        roleName: [
          { required: true, message: '', trigger: 'blur' },
          { min: 1, max: 10, message: ' 1  10 ', trigger: 'blur' }
        ],
        roleDesc: [
          { required: true, message: '', trigger: 'blur' }
        ]
      },
      // 
      editRoleDialogVisible: false,
      // id
      editId: ''
    }
  },
  // vue
  created() {
    this.initPages()
  },
  methods: {
    // ,
    async initPages() {
      const { data: res } = await this.$http.get('/roles')
      // console.log(res)
      if (res.meta.status !== 200) return this.$Message.error(res.meta.msg)
      this.tableData = res.data
    },
    // 
    async removeRightById(role, rightId) {
      const confirmResult = await this.$confirm(
        ', ?',
        '',
        {
          confirmButtonText: '',
          cancelButtonText: '',
          type: 'warning'
        }
      ).catch(err => err)
      if (confirmResult === 'cancel') {
        return this.$Message.info('')
      }
      const { data: res } = await this.$http.delete(
        `/roles/${role._id}/rights/${rightId}`
      )
      if (res.meta.status !== 200) return this.$Message.error(res.meta.msg)
      role.children = res.data
      this.$Message.success('')
    },
    // 
    async showSetRightDialog(role) {
      this.roleId = role._id
      this.showSetRightdialogVisible = true
      // 
      const { data: res } = await this.$http.get('/rights/tree')
      if (res.meta.status !== 200) return this.$Message.error(res.meta.msg)
      // rightsList
      this.rightsList = res.data
      console.log(this.rightsList)
      // 
      this.getLeafKey(role, this.defaultKeys)
      // console.log(this.rightsList)
    },
    // id,defKeys
    getLeafKey(node, arr) {
      // children
      if (!node.children) {
        return arr.push(node.oid)
      }
      node.children.forEach(item => {
        this.getLeafKey(item, arr)
      })
    },
    // 
    setRightDialogClosed() {
      this.defaultKeys = []
    },
    // 
    async allotRight() {
      const keys = [
        ...this.$refs.treeRef.getCheckedKeys(),
        ...this.$refs.treeRef.getHalfCheckedKeys()
      ]
      console.log(keys)
      const { data: res } = await this.$http.post(
        '/roles/' + this.roleId + '/rights',
        {
          rids: keys
        }
      )
      if (res.meta.status !== 200) return this.$Message.error(res.meta.msg)
      this.$Message.success(res.meta.msg)
      this.showSetRightdialogVisible = false
      this.initPages()
    },
    // 
    addRole() {
      this.addUserdialogVisible = true
    },
    // 
    async saveRole() {
      this.$refs.addRoleRef.validate(async valide => {
        // 
        if (!valide) {
          return false
        }
        const { data: res } = await this.$http.post('/roles', this.addRoleForm)
        if (res.meta.status !== 200) return this.$Message.error(res.meta.msg)
        this.addUserdialogVisible = false
        this.$Message.success(res.meta.msg)
        //  
        this.initPages()
      })
    },
    // 
    addDialogClose() {
      // 
      this.$refs.addRoleRef.resetFields()
    },
    // 
    showEditRole(role) {
      console.log(role)
      this.editRoleForm.roleName = role.roleName
      this.editRoleForm.roleDesc = role.roleDesc
      this.editId = role._id
      this.editRoleDialogVisible = true
    },
    // 
    async saveEditRole() {
      this.$refs.editRoleRef.validate(async valide => {
        // 
        if (!valide) {
          return false
        }
        const { data: res } = await this.$http.put(
          '/roles/' + this.editId,
          this.editRoleForm
        )
        if (res.meta.status !== 200) return this.$Message.error(res.meta.msg)
        this.editRoleDialogVisible = false
        this.$Message.success(res.meta.msg)
        //  
        this.initPages()
      })
    },
    // 
    async delectRole(id) {
      const isVauleUser = await this.$confirm(
        ', ?',
        '',
        {
          confirmButtonText: '',
          cancelButtonText: '',
          type: 'warning'
        }
      ).catch(err => err)
      console.log(isVauleUser)
      if (isVauleUser === 'cancel') return this.$Message.info('')
      const { data: res } = this.$http.delete('/roles/' + id)
      if (res.meta.status !== 200) return this.$Message.error(res.meta.msg)
      //  
      this.initPages()
      this.$Message.success(res.meta.msg)
    }
  }
}
</script>
<style lang="less" scoped>
.el-tag {
  margin: 7px;
}
.dbtop {
  border-top: 1px solid #eee;
}
.dbbottom {
  border-bottom: 1px solid #eee;
}
.vcenter {
  display: flex;
  align-items: center;
}
</style>
